body {
    padding: 0;
    margin: 0;
  }
  
  #menu {
    background-color: black;
    padding: 0;
    margin:  0;
  }
  
  #menu li{
    display: inline-block;
    margin: 0 10px;
    padding: 0;
  }
  
  #menu a {
    text-decoration: none;
    display:inline-block;
    color: white;
    padding: 10px;
  }
  
  #menu a:hover {
    background-color:grey;
  }
  
  #hero {
    background-image: url(jap-food-1.jpg);
    width: 100%;
    height: calc(100vh - 40px); 
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
  }
  
  
  #cta {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 300px;
    height:50px;
    background-color: rgba(255,255,255,0.5);
    text-align:center;
    transform: translate(-50%, -50%);
    border:1px black solid;
    padding-top:20px;
  }
  
  #cta a{
    text-decoration: none;
    color: black;
    font-size: 32px;
  }
  
  #about-us {
    background-image:url(jap-food-2.jpg);
    width: 100%;
    height: 90vh;
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    margin: 0; 
    padding: 0.5px;
    
  }
  
  #about-us .content {
    background-color: rgba(0,0,0,0.5);
    height: 100%;
    width: 100%;
    margin: 0;
    padding: 0.1px; /* margin collapse! */
  }
  
  #about-us .content h1 {
    color: white;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    padding-left: 1em;
  }